<script lang="js">
import Vue from 'vue'
export default{
    data(){
        return{
          diagnosis:{
            diagnosis_doctor_name:"",
            cp_diagnosis_code:"",//初步诊断
            acs_delivery_time:'',//抗血小板首次给药时间
            aspirin_dose:'',//阿司匹林剂量
            acs_drug_type:"",//1 氯吡格雷 2替格瑞洛
            is_anticoagulation:"",//首次抗凝给药
            thrombolysis_type:'',
            is_direct:'',//是否直达1 是 2否
            throm_treatment_place:'',//溶栓场所  选填（1 其他医院 2 救护车 3 本院急诊科 4 本院心内科，传1,2,3,4或空值）
            is_thrombolysis:'',//1 有 0 无
            start_agree_time:"",//开始知情同意时间
            sign_agree_time:"",//签署知情同意  选填
            throm_start_time:'',//溶栓开始时间
            throm_end_time:"",//溶栓结束时间
            throm_drug_type:"",//溶栓药物
            throm_drug_code:"",//溶栓剂量
            condition_desc:"",//患者情况备注
            screening:"",//溶栓检查
          },
          ruleValidate:{},
          AcsShow:true,
            GraceShow:false,
          therapyShow:false,
          drugList:[],//药物
          unitList:[
            {
                id:'mg',
              name:'mg'
            },
          ]
        }

    },
  created(){
    var pThis=this;
    pThis.GetBaseValue();
//    pThis.patientinfo.AidButton = false;
//    alert(pThis.patientinfo.AidButton)
    pThis.codeGet();
  },
//  mounted(){
//    var height = $(window).height()-50-10-118-20-46-90-46-46-150-10;
//    $(".thrombolysisDiv").css("height",height);
//  },
    methods:{
      scrollChange(ev){
        var div = document.getElementById('diagnosis');
//        if (div.scrollHeight) {
//          var height = div.scrollHeight;
//          alert(height)
          setTimeout(function () {
            $("#diagnosis").scrollTop(ev*40)
          }, 500)
//        }
      },
      codeGet(){  //字典查询
        var pThis = this;
        var codes = {code:["ANTICOAGULATION"]};//年龄单位 随车人员 车辆 手环编号 实时监控盒子编号
        this.$http.post(pThis.host('defdoc/getDefdocViewByCodes'),codes)
          .then(function (res) {
            let datajson = res.data;
            let backjson={"state":datajson.state,"msg":datajson.msg};
            pThis.backLogin(backjson);
            if(datajson.state=='1'){
              var data=datajson.results[0];
              for(var key in data){
                if(key=='ANTICOAGULATION'){//年龄单位
                  pThis.drugList=data[key];
                  console.log(pThis.drugList)
                }
              }
            }
          }).catch(function (error) {
          console.log(error)
        });
      },
      timeChange(date){
          console.log(date)
        if(typeof date =='object'){
          var year=date.getFullYear();
          var month=date.getMonth()+1 <=9 ?"0"+(date.getMonth()+1):date.getMonth()+1;
          var day=date.getDate() <=9 ?"0"+date.getDate():date.getDate();
          var hours=date.getHours() <=9 ?"0"+date.getHours():date.getHours();
          var mins=date.getMinutes() <=9 ?"0"+date.getMinutes():date.getMinutes();
//          var seds=date.getSeconds() <=9 ?"0"+date.getSeconds():date.getSeconds();
          return year+"-"+month+"-"+day+" "+hours+":"+mins;
        }
      },
      getGrace(){
          var pThis = this;
          var data = {
            age_value:pThis.patientinfo.padPatInfoView.age_value,//年龄
            heart_rate:pThis.patientinfo.padCheckInfoView.heart_rate,//心率
            blood_pressure_high:pThis.patientinfo.padCheckInfoView.blood_pressure_high,//收缩压
            cr_value:pThis.patientinfo.padCheckInfoView.cr_value,//肌酐
//            killip:pThis.patientinfo.padCheckInfoView.killip_level,//killip分级
            killip:'Ⅲ',//killip分级
            is_rise:pThis.patientinfo.padDiaInfoView.is_rise,//心肌坏死标志物是否升高
            is_arrest:pThis.patientinfo.padDiaInfoView.is_arrest,//发病后曾出现心脏骤停
            is_change:pThis.patientinfo.padDiaInfoView.is_change,//心电图ST段是否改变
          };
          if(data.age_value ==''){
            pThis.$toast.center('fail.png', '年龄未填写');
          }else if(data.heart_rate ==''){
            pThis.$toast.center('fail.png', '心率未填写');
          }else if(data.blood_pressure_high ==''){
            pThis.$toast.center('fail.png', '收缩压未填写');
          }else if(data.cr_value ==''){
            pThis.$toast.center('fail.png', '肌酐未填写');
          }else if(data.killip ==''){
            pThis.$toast.center('fail.png', 'killip分级未填写');
          }else if(data.is_rise =='0' && data.is_arrest =='0' && data.is_change =='0'){
            pThis.$toast.center('fail.png', 'Grace评估未填写');
          }else {
            pThis.$http.post(pThis.host("firstaid/computeGracevalue"), data)
              .then(function (response) {
                let datajson = response.data;
                if (datajson.state == "1") {
                  pThis.patientinfo.padDiaInfoView.grace_value = response.data.results[0].grace_value;
                  if(pThis.patientinfo.padDiaInfoView.grace_value <='108'){
                    pThis.patientinfo.padDiaInfoView.risk_lamination = '4';
                  }else if('109'<pThis.patientinfo.padDiaInfoView.grace_value <='140'){
                    pThis.patientinfo.padDiaInfoView.risk_lamination = '3';
                  }else {
                    pThis.patientinfo.padDiaInfoView.risk_lamination = '2';
                  }
                }
              }).catch(function (error) {
              console.log(error);
            });
          }
      },
      timeGet(){
        var pThis=this;
          pThis.$http.post(pThis.host("task/getdatetime"))
            .then(function (response) {
              pThis.patientinfo.padTimeView.diagnosis_time =response.data.results[0];
            })
            .catch(function (error) {
              console.log(error);
            });
      },
        ACS1(event){
          this.AcsShow=true;
            this.GraceShow=false;
            this.therapyShow=false;
          $(".bgBtn").removeClass('modelTitleActive');
          $(event.target).addClass('modelTitleActive');
        },
      ACS2(event){
        this.AcsShow=false;
          this.GraceShow=false;
          this.therapyShow=true;
        $(".bgBtn").removeClass('modelTitleActive');
        $(event.target).addClass('modelTitleActive');
      },
      ACS3(event){
          this.AcsShow=false;
          this.GraceShow=true;
          this.therapyShow=false;
        $(".bgBtn").removeClass('modelTitleActive');
        $(event.target).addClass('modelTitleActive');
      },
      CheckChange(value){   //全局变量值交换
        var pThis = this;
        pThis.patientinfo.padDiaInfoView[value]= pThis.diagnosis[value];
      },
      GetBaseValue(){   //全局变量赋值
        var pThis = this;
        for(var value in pThis.patientinfo.padDiaInfoView){
          pThis.diagnosis[value] = pThis.patientinfo.padDiaInfoView[value];
        }
      },
      Rule(ev,json,num,rule){
        if (ev.keyCode == 37 || ev.keyCode == 39) {
          return;
        }else {
          if(!num){
            num=3
          }
          if(!rule){
//            rule=/[^\d]/g
//            rule=/[^\u4e00-\u9fa5]{0,}$/
            rule=''
          }
//          ev.target.value = ev.target.value.replace(/[^\-?\d.]/g,'');
          ev.target.value = this.validate.formatNum(rule,num, ev)
          Vue.set(this.patientinfo[json[0]], json[1], ev.target.value)
        }
      },
    }
}
</script>
